import {Directive, Input} from '@angular/core';
import {AdaptChartAbstract} from './chart.model';

@Directive()
export abstract class AdaptPieChartAbstract extends AdaptChartAbstract {
  @Input() innerRadius?: number;
  @Input() abstract series: AdaptPieData[];
}

export interface AdaptPieData {
  name: string;
  hidden?: boolean;
  y: number;
}

export interface AdaptPieClickEvent {
  event: MouseEvent;
  data: AdaptPieData;
  color: string;
  category: string;
  value: number;
  total: number;
}
